<template>
    <div v-if="ready">
        <div v-if="open" class="inner_item">
            <form @submit.prevent="update_record()">
                <div class="card col-12" style='height:100%'>
                    <div class="card-header">
                        <div class="flex">
                            <strong>View Details</strong>
                            <i class="fa fa-window-close" aria-hidden="true" style='margin-left:auto;margin-top:0.25rem;margin-right:0.5rem;cursor:pointer' @click="close_overlay"></i>
                        </div>
                    </div>            
                    <div class="card-body card-block">
                        <div class="col-12 flex px0 inner_row">
                            <div class="col-4">
                                Item Code
                            </div>
                            <div class="col-8">
                                <input type="text" class="form-control" v-model="info.item_code" readonly/>
                            </div>                    
                        </div>
                        <div class="col-12 flex px0 inner_row">
                            <div class="col-4">
                                Item Name
                            </div>
                            <div class="col-8">
                                <input type="text" class="form-control" v-model="info.item_eng_name" readonly/>
                            </div>                    
                        </div>
                        <div class="col-12 flex px0 inner_row">
                            <div class="col-4">
                                Borrowed Date
                            </div>
                            <div class="col-8">
                                <input type="date" class="form-control" v-model="info.borrow_date" readonly/>
                            </div>                    
                        </div>
                        <div class="col-12 flex px0 inner_row">
                            <div class="col-4">
                                Borrowed By
                            </div>
                            <div class="col-8">
                                <input type="text" class="form-control" v-model="info.borrow_by" readonly/>
                            </div>                    
                        </div>
                        <div class="col-12 flex px0 inner_row" v-if="(info.status=='returned')&&(info.return_date!='0000-00-00')">
                            <div class="col-4">
                                Return Date
                            </div>
                            <div class="col-8">
                                <input type="date" class="form-control" v-model="info.return_date" readonly/>
                            </div>                    
                        </div>
                        <div class="col-12 flex px0 inner_row">
                            <div class="col-4">
                                Status
                            </div>
                            <div class="col-8">
                                <select class="form-control select" v-model="new_stat" v-if="info.status=='borrowed'">
                                    <option value="returned">Returned</option>
                                    <option value="borrowed">Borrowed</option>
                                </select>
                                <input type="text" class="form-control" readonly value="Returned" v-if="info.status=='returned'"/>
                            </div>                    
                        </div>
                    </div>
                    <div class="card-footer" v-if="info.status!='returned'">
                        <div class="flex">
                            <button type="submit" class="btn btn-primary btn-sm">
                                <i class="fa fa-paper-plane"></i> Submit
                            </button>                   
                        </div>
                    </div>
                </div>
            </form>
        </div>
        <div class="col-12 px0">
            <div class="col-12 px0 flex" style="margin-bottom:1rem">
                <div class="col-4 px0">
                    <input type="text" class="form-control" v-model="key" placeholder="Live Search" @keyup="search_record()"/>
                </div>              
                <div class="col-2">
                    <select class="form-control select" v-model="status" @change="update_list">
                        <option value="all">View All Records</option>
                        <!--<option value="pending">View Borrow Requests</option>-->
                        <option value="borrowed">View Borrowed Records</option>
                        <option value="returned">View Returned Records</option>
                    <select>
                </div>
                <div class="col-4"></div>
                <div class="col-2 px0">
                    <button type="button" class="form-control" @click="new_record">New Record</button>
                </div>                
            </div>
            <v-data-table
                :headers="headers"
                :items="data"
                :rows-per-page-items="[15]"
                class="elevation-1"                
                disable-initial-sort
                show-select              
                item-key="item_code"               
            >          
            <template v-slot:items="props">        
                <tr style='cursor:pointer' @click="view_details(props.item)">
                    <td>{{ props.item.item_eng_name }}</td>
                    <td>{{ props.item.item_code }}</td>
                    <td>{{ props.item.borrow_by }}</td>
                    <td>{{ props.item.borrow_date }}</td>
                    <td>{{ props.item.return_date }}</td>
                    <td><u :class="props.item.status">{{ props.item.status }}</u></td>
                </tr>                             
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>  
            </template>
            </v-data-table>
        </div>
    </div> 
</template>
<script>
export default {
    data(){
        return{
            open:false,
            new_stat:'',
            info:{},  
            data:[],
            key:'',
            status:'all',
            ready:false,
            headers:[],
            headers_all: [               
                { text: 'Name',value:'item_eng_name',sortable: true},  
                { text: 'Item Code',value :'item_code',sortable: true},              
                { text: 'Borrowed by',value:'borrow_by',sortable: true},
                { text: 'Borrow Date',value:'borrow_date',sortable: true},
                { text: 'Return Date',value:'return_date',sortable: true},
                { text: 'Status',value:'status',sortable: true},
            ],
        }
    },
    async created(){
        this.headers = this.headers_all;
        await this.fetch_details();
        this.ready=true;
    },
    mounted(){

    },
    methods:{
        close_overlay(){
            this.open = false;
        },
        update_record(){
            var formData = new FormData();
            formData.append('item_code',this.info.item_code);
            formData.append('status',this.new_stat);
            axios.post('inventory/web/backend/update_borrow.php',formData)
                .then(
                    (response)=>{
                        if(response.data=="OK"){
                            this.$alert("Successfully updated record")
                                .then(
                                    ()=>{
                                        this.open = false;
                                        this.update_list();
                                    }
                                )
                        }
                    }
                )
        },
        new_record(){
            this.$router.push('NewBorrowRecord');
        },
        async update_list(){
            await this.fetch_details();
            this.$forceUpdate();
        },
        search_record(){
            var formData = new FormData();
            formData.append('key',this.key);
            formData.append('status',this.status);
            axios.post('inventory/web/backend/search_borrow.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                        } else {
                            this.data = [];
                        }
                    }
                )
        },
        view_details(item){
            this.info = item;
            this.new_stat = item.status;
            this.open = true;
        },
        fetch_details(){
            var formData = new FormData();
            formData.append('status',this.status);
            axios.post('inventory/web/backend/borrow_record.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                        } else {
                            this.data=[];
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
    .flex{
        display:flex;
        flex-direction: row;
    }
    .px0{
        padding-left:0;
        padding-right:0;
    }
    .click_row{
        cursor:pointer;
    }
    .returned{
        font-weight: 600;
        text-transform: capitalize;
        color:rgb(10,138,65)!important;
    }
    .pending{
        font-weight: 600;
        text-transform: capitalize;
        color:rgb(219,20,20)!important;
    }
    .borrowed{
        font-weight: 600;
        text-transform: capitalize;
        color:rgb(7,75,148)!important;
    }
    .inner_item{
        position: absolute; 
        left: 0; 
        right: 0; 
        margin-top: 15vh;
        margin-left: auto; 
        margin-right: auto; 
        width: 40%;
        z-index: 100;
    }
    .fa-window-close{
        transition: all 0.15s ease-in-out;
        color:rgba(255, 255, 255, 0.55);
    }
    .fa-window-close:hover{
        color:rgba(255, 255, 255, 1);
    }
    .inner_row{
        display:flex;
        flex-direction:row;
        margin-bottom:1rem;
    }
</style>


